<template>
  <div class="notice">
    <img class="header_img" src="../../assets/img/Notice/mine_img_10.jpg" alt />
    <p class="title">{{$t('notice.list[0]')}}</p>
    <div class="center" v-if="zixunLi">
      <div class="flex" v-for="item in zixunLi.res" :key="item.id" @click="goDetail(item.id)">
        <p class="flex_p">{{item.title}}</p>
        <p class="flex_p">{{$public.timestampToTime(item.create_time)}}</p>
      </div>

      <!-- <el-pagination
        @current-change="handleCurrentChange"
        class="goDeil"
        background
        layout="prev, pager, next"
        :page-size="10"
        :total="Number(zixunLi.count)"
      ></el-pagination> -->
      <div style="margin-top: 60px">
          <pagination :page="page" @change="handleCurrentChange"> </pagination>  
      </div>

    </div>
  </div>
</template>
<style lang='less'>


@import "../../assets/index.less";
@import "notice.less";
</style>
<script>
  
import  pagination from "@/components/pagination";
import { zixun } from "../../api/c2c";
export default {
  data() {
    return {
      page:{
        p: 1,
        size:10,
        total:10
      }, 
      zixunLi: null,
    };
  },
  created() {
    this.zixunList();
  },
  components:{
    pagination
  },
  methods: {
    handleCurrentChange(p) {
      this.p = p;
      this.zixunList();
    },
    goDetail(id) {
      this.$router.push({ name: "noticeData", query: { id: id } });
    },
    zixunList() {
      var _this = this;
      zixun({ p: this.p, size: this.page.size }).then(res => {
        switch (res.status == 200) {
          case true:
            _this.zixunLi = res.data.data;
            this.page.total = res.data.data.count
            break;
          default:
            _this.$public.msg(res.data.msg, "error", _this);
            break;
        }
      });
    }
  }
};
</script>



